<template lang="html">
  <x-header>
    登录
  </x-header>
  <div class="weui_cells weui_cells_form">
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-mobile"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="手机号码" v-model="mobile" />
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-lock"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="password" placeholder="密码" v-model="password" />
      </div>
    </div>
  </div>
  <div class="button_sp_area" style="margin-top:30px;padding: 10px;">
    <a @click="signIn" href="javascript:;" class="weui_btn weui_btn_plain_primary">登 录</a>
  </div>
  <divider>没账号?</divider>
  <div class="button_sp_area" style="margin-top:5px;padding: 10px;">
    <a v-link="{ name: 'signup' }" class="weui_btn weui_btn_plain_primary">注 册</a>
  </div>
  <toast :show.sync="loginFail" type="text">登录失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Divider from 'vux/dist/components/divider'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        mobile: '',
        password: '',
        loginFail: false
      }
    },
    route: {
      data (transition) {
        let token = localStorage.getItem('ruzun_user_token')
        if (token && token !== null) {
          this.$route.router.go({name: 'home'})
        }
      }
    },
    methods: {
      signIn () {
        if (this.mobile === '' && this.password === '') {
          this.$set('loginFail', true)
          return
        }
        this.$http.post(this.$config.API + '/sessions.json', {mobile: this.mobile, password: this.password}).then(function (response) {
          // 登录成功
          console.log(response.headers)
          // if (typeof response.data === 'string') {
          //  response.data = JSON.parse(response.data)
          // }
          console.log(response)
          console.log(response.data)
          localStorage.setItem('ruzun_user_token', response.data.user.token)
          localStorage.setItem('ruzun_user_id', response.data.user.id)
          this.$route.router.go({name: 'home'})
        }, function (response) {
          // 注册失败
          this.$set('loginFail', true)
        })
      }
    },
    components: {
      XHeader,
      Divider,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



